import PropTypes from 'prop-types';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faTwitter from '@fortawesome/fontawesome-free-brands/faTwitter'
import faFacebook from '@fortawesome/fontawesome-free-brands/faFacebook'
import faInstagram from '@fortawesome/fontawesome-free-brands/faInstagram'
import faGithub from '@fortawesome/fontawesome-free-brands/faGithub'
import {ScreenContainer} from "../gigya";
import {Screen} from "../gigya/screens";
import React from "react";

export class AuthScreen extends React.Component {
    render() {

        let close = <div className="close" onClick={() => {this.props.onCloseArticle()}}></div>

        return (
            <div id="auth" style={this.props.timeout ? {display: 'flex'} : {display: 'none'}}>

                <ScreenContainer>
                    <Screen screenSet="Default-RegistrationLogin" startScreen='gigya-register-screen' />;
                    {close}
                </ScreenContainer>
            </div>
            

        )
    }
}

AuthScreen.propTypes = {
    route: PropTypes.object,
    screenSet: PropTypes.string,
    startScreen: PropTypes.bool,
    isActive: PropTypes.bool,
    timeout: PropTypes.bool
}

